:root{
    --primaryColor:hsl(26,93%,50%);
    --HoverColor:hsl(26,90%,42%);
    --whiteColor:hsl(0,0%,100%);
    --blackColor:hsl(0,0%,18%);
    --textColor:hsl(240,1%,48%);
    --whiteColorDeam:hsl(0,0%,93%);
    --greyBg:hsl(0,0%,96%);
    --greyText:rgb(190,190,190);
    --inputColor:hsl(330,12%,97%);
    /* box-shadow: 0 2px 8px 2px rgba(178,178,178,0.45); */
    
    }
    .about{
        .secContainer{
            .title{
                color: var(--blackColor);
                line-height: 1.5rem;
                padding-bottom: 2rem;
                text-align: center;
                font-size: 1.5rem;
            }
            .mainContent{
                gap: 1rem;
                .singleItem{
                    text-align: center;
                    img{
                        border-radius: 50%;
                        width: 50%;
                        padding: 1rem;
                       
                    }
                    h3{
                        font-weight: 700;
                        color: var(--blackColor);
                        padding: .5rem;
                    }
                    p{
                        font-weight: 500;
                        font-size: 13px;
                        padding-bottom: .5rem;
                        color: var(--textColor);

                    }
                }

            }
            .videoCard{
              margin-top: 2rem;
              background: linear-gradient(to top, rgba(0,0,0,0.73),
              rgba(0,0,0,0.609)),
              url(../../Assets/map4.jpg);
              background-position: center;
              background-repeat: no-repeat;
              background-size: cover;
              border-radius: 10px;
              padding: 1rem;
              .cardContent{
                padding:2rem 1rem;
                gap: 2rem;
                .cardText{
                    h2{
                        color: var(--whiteColor);
                        font-weight: 600;
                        padding-bottom: 1rem;
                        text-align: center;
                    }
                    p{
                        color: var(--whiteColorDeam);
                        opacity: 0.8;
                        font-size: 12px;
                        letter-spacing: 1px;
                        text-align: center;

                    }
                }
                .cardVideo{

                    width: 100%;
                    height: 200px;
                    border: 2px solid var(--whiteColor);
                    border-radius: 10px;
                    overflow: hidden;
                    .video{
                        width: 100%;

                        height: 100px;
                        object-fit: cover;

                    }


                }
              }
            
                
            }
        }
    }
    @media screen and (min-width:700px){
        .about{
            .secContainer{
                .mainContent{
                    grid-template-columns: repeat(3,1fr);
                    .singleItem{

                        p{
                            font-size: 10px;
                            
                        }
                    }
                }
                .videoCard{
                    .cardContent{
                        gap: 4rem;
                        padding: 4rem 3rem;
                        grid-template-columns: repeat(2,1fr) !important;
                        .cardText{
                            h2{
                                font-size: 1.5rem;
                            }
                            p{
                                opacity: .7;
                                font-size: 15px;
                            }
                        }
                        .cardVideo{
                            
                            video{
                                width: 100%;

                                height: 195px;
                                object-fit: cover;
                            }
                        }
                    }

                }
            }
        }


    }
