:root {
  --primaryColor:hsl(26,93%,50%);
  --HoverColor:hsl(26,90%,42%);
  --whiteColor:hsl(0,0%,100%);
  --blackColor:hsl(0,0%,18%);
  --textColor:hsl(240,1%,48%);
  --whiteColorDeam:hsl(0,0%,93%);
  --greyBg:hsl(0,0%,96%);
  --greyText:rgb(190,190,190);
  --inputColor:hsl(330,12%,97%);
}

.offers .secContainer .thirdContent {
  gap: 1rem;
}
.offers .secContainer .thirdContent .singleOffer {
  border-radius: 1rem;
  overflow: hidden;
  padding: 0.5rem;
  box-shadow: 0 2px 8px 2px rgba(207, 205, 205, 0.45);
}
.offers .secContainer .thirdContent .singleOffer .icon {
  font-size: 20px;
  color: var(--textColor);
}
.offers .secContainer .thirdContent .singleOffer small {
  font-weight: 500;
  color: var(--textColor);
}
.offers .secContainer .thirdContent .singleOffer .destImage {
  position: relative;
  height: 200px;
  width: 100%;
  border-radius: 1rem;
  overflow: hidden;
}
.offers .secContainer .thirdContent .singleOffer .destImage img {
  height: 100%;
}
.offers .secContainer .thirdContent .singleOffer .destImage .discount {
  position: absolute;
  top: 10px;
  right: 10px;
  padding: 5px 10px;
  background: var(--primaryColor);
  color: var(--whiteColor);
  border-radius: 3rem;
  font-size: 10px;
  font-weight: 500;
}
.offers .secContainer .thirdContent .singleOffer .offerBody {
  padding: 1rem;
}
.offers .secContainer .thirdContent .singleOffer .offerBody .price {
  justify-content: space-between;
}
.offers .secContainer .thirdContent .singleOffer .offerBody .price h4 {
  color: var(--blackColor);
  font-weight: 700;
  font-size: 1.1rem;
}
.offers .secContainer .thirdContent .singleOffer .offerBody .price .status {
  padding: 5px 10px;
  color: var(--HoverColor);
  background: var(--greyBg);
  font-size: 10px;
  font-weight: 600;
  border-radius: 3rem;
}
.offers .secContainer .thirdContent .singleOffer .offerBody .amenities {
  justify-content: flex-start;
  flex-wrap: wrap;
  width: 100%;
  padding: 1rem 0;
  gap: 0.5rem;
}
.offers .secContainer .thirdContent .singleOffer .offerBody .amenities .singleAmenity {
  padding: 5px 10px;
  border-radius: 5px;
  background: var(--greyBg);
  gap: 0.5rem;
}
.offers .secContainer .thirdContent .singleOffer .offerBody .btn {
  margin-top: 1rem;
  width: 100%;
  color: var(--whiteColor);
  text-align: center;
  justify-content: center;
  font-weight: 600;
  gap: 0.5rem;
  transition: 0.1 ease;
}
.offers .secContainer .thirdContent .singleOffer .offerBody .btn .icon {
  color: var(--whiteColor);
}
.offers .secContainer .thirdContent .singleOffer .offerBody .btn:hover {
  background: var(--whiteColor);
  border: 1px solid;
  color: var(--primaryColor);
}
.offers .secContainer .thirdContent .singleOffer .offerBody .btn:hover .icon {
  color: var(--primaryColor);
}
.offers .secContainer .thirdContent .singleOffer:hover {
  transform: translateY(-7px);
  box-shadow: 0 4px 10px 4px rgba(59, 56, 56, 0.45);
}/*# sourceMappingURL=Offers.css.map */